@import "../../theme/local.scss";
.cars-tab {
  padding-top: $size-mid-locked;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    width: 6px;
    border-left: 1px solid $color-disabled;
  }

  &::-webkit-scrollbar-track {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $color-second;
    border: 2px solid transparent;
    border-radius: 2px;
    background-clip: content-box;
  }
  .cars-tab__radios {
    margin-bottom: 48px;
    .radio-container {
      margin-right: 16px;
    }
  }
  .cars-tab__table {
    padding: 1px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    margin-bottom: $size-small-locked;  
  }
  @media only screen and (min-width: 0px) and (max-width: 767px) {
    .cars-tab__radios{
      display: flex;
      flex-direction: column;
    }
  }
}
